.overflow{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0,0,0, .5);
    padding-top: 10%;
}

.window{
    display: block;
    width: 400px;
    height: 230px;
    background-color: #ffffff;
    margin: auto;
}

.header{
    display: block;
    height: 30px;
    width: 400px;
    border-bottom: 1px solid #808080;
    padding-top: 4px;
}

.header__button{
    display: block;
    width: 20px;
    height: 20px;
    margin-left: auto;
    margin-right: 20px;
    background-color: #FF2D1E;
    border: none;
    outline: none;
    padding: 0;
}

.header__button p{
    font-size: 20px;
    display: block;
    margin: auto;
    transform: rotate(40deg);
}

.body{
    display: block;
    max-width: 400px;
    max-height: 100px;
    margin: 0 auto;
}

.body__title{
    font-size: 24px;
}

.body__name{
    outline: none;
    padding: 2px;
    width: 350px;
    font-size: 14px;
    margin-bottom: 10px;
}
.body_error{
    display: block;
    width: 400px;
    height: 20px;
    margin: 0 auto;
    text-align: center;
    color: red;
}

.body_error p{
    display: block;
    width: 400px;
    height: 20px;
    margin: 0 auto;

}

.footer{
    display: flex;
    justify-content: center;
    margin-top: 10px;
}


.footer__cancel{
    margin-right: 10px;
    width: 100px;
    height: 30px;
    font-size: 16px;
    background-color: #FF2D1E;
    outline: none;
    border: 1px solid #808080;
    text-align: center;
    color: #ffffff;
}

.footer__submit{
    margin-right: 10px;
    width: 100px;
    height: 30px;
    font-size: 16px;
    background-color: #5AD3FF;
    outline: none;
    border: 1px solid #808080;
    text-align: center;
    color: #ffffff;
}