.task__container{
    display: flex;
    justify-content: center;
    border: solid 1px #818384;
    border-radius: 10%;
    max-width: 600px;
    margin: 10px auto;
}

.task__delete{
    width: 20px;
    height: 10px;
    margin: auto 0;
    background-color: #C90000;
    border: none;
    border-radius: 50%;
    outline: none;
}

.number{
    width: 20px;
    height: 20px;
    margin-right: 50px;
}

.task__name{
    width: 350px;
    margin-right: 20px;
}
