.container{
    max-width: 1200px;
    margin: 0 auto;
}

.window{
    margin: 300px 0 0 0;
    text-align: center;
}

.window__create{
    display: flex;
    justify-content: center;
    max-width: inherit;
    text-align: center;
}

.window__create_add{
    display: block;
    margin-right: 10px;
}

.window__create_plus{
    width: 20px;
    height: 20px;
    text-align: center;
    background-color: #fff;
    border: none;
    margin: auto 0;
    font-size: 22px;
    outline: none;
}
